.content {
  max-width: 1000px;
  margin: 0 auto;
  padding: 50px 0;
}

.title {
  font-family: "Monoton", cursive;
  font-size: 50px;
  font-weight: 700;
  color: #fff;
  margin: 0;
}
.title::selection {
  background-color: rgba(255, 197, 24, 1);
}
.title::-moz-selection {
  background-color: rgba(255, 197, 24, 1);
}
.subtitle {
  font-family: Georgia;
  font-size: 20px;
  color: rgba(255, 255, 255, 0.8);
  margin-top: 30px;
  max-width: 500px;
}
.subtitle::selection {
  background-color: rgba(255, 197, 24, 1);
}
.subtitle::-moz-selection {
  background-color: rgba(255, 197, 24, 1);
}
.list {
  margin-top: 100px;
}
.grid {
  list-style: none;
  margin: 0;
  padding: 0;
  column-count: 3;
  column-gap: 20px;
  column-rule-width: 0;
}
.grid-item {
  display: inline-block;
  margin-bottom: 20px;
}
.grid-item img {
  display: block;
  max-width: 100%;
}
